/* General styles for the prediction page */
.prediction-container {
    background-image: url('../Images/bg.jpg'); /* Ensure this path is correct */
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    min-height: 100vh;
    padding: 2rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start; /* Start aligning content from the top */
    color: white; /* Set default text color to white */
}

/* Title styles */
.title {
    margin-bottom: 2rem; /* Move title down */
    color: white; /* Ensure title text is white */
    text-align: center;
}

/* File Upload Section */
.file-upload-container {
    margin-bottom: 2rem; /* Move file upload section down */
    text-align: center;
}

/* Container for main content */
.main-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 800px; /* Limit width for centering */
    width: 100%;
}

/* Button styles */
.button-container {
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
}

/* Primary Button styles */
.btn-primary {
    background-color: #007bff;
    border-color: #007bff;
    color: white; /* Ensure button text is white */
    border-radius: 0.25rem;
    padding: 0.5rem 1.5rem;
    font-size: 1rem;
    margin: 0.5rem;
}

.btn-primary:hover {
    background-color: #0056b3;
    border-color: #004085;
}

/* Clear Button styles */
.btn-clear {
    background-color: blue; /* Blue background */
    color: white; /* White text */
    border: none;
    border-radius: 0.25rem;
    padding: 0.5rem 1.5rem;
    font-size: 1rem;
    margin: 0.5rem;
}

.btn-clear:hover {
    background-color: darkblue; /* Darker blue on hover */
}

/* Table styles */
.table-container {
    max-width: 600px; /* Adjust to make the table smaller */
    margin-bottom: 2rem;
    overflow-x: auto; /* Allow horizontal scroll if necessary */
    display: flex;
    justify-content: center;
}

.table {
    color: white; /* Set table text color to white */
    border-collapse: collapse;
    width: 100%;
}

.table th, .table td {
    padding: 0.5rem; /* Adjust padding for smaller table cells */
    vertical-align: middle;
}

.table-striped tbody tr:nth-of-type(odd) {
    background-color: #343a40; /* Dark background for odd rows */
}

.table-bordered {
    border: 1px solid #dee2e6;
}

.table-bordered th, .table-bordered td {
    border: 1px solid #dee2e6;
}

/* Spinner styles */
.spinner-container {
    display: flex;
    justify-content: center;
    width: 100%;
    margin: 2rem 0;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .title {
        font-size: 1.5rem;
    }

    .btn-primary, .btn-clear {
        width: 100%;
    }

    .form-group {
        margin-bottom: 1rem;
    }

    .table-container {
        overflow-x: auto;
    }
}
