.app__dashboard {
  height: fit-content;
  background: var(--secondary-color);
  padding: 10% var(--md-gap);
}

.dashboard__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.left__dashboard {
  display: flex;
  flex-direction: column;
  gap: var(--lg-gap);
  flex: 1;
}

.left__text {
  display: flex;
  flex-direction: column;
  gap: var(--lg-gap);
}

.left__text h1 {
  font-size: 3.5rem;
  color: var(--primary-color);
}

.left__text p {
  color: var(--neutral-color);
  font-size: 1.2rem;
}

.left__dashboard__container {
  display: flex;
  align-items: center;
  gap: var(--lg-gap);
}

.control__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--md-gap);
  cursor: pointer;
  color: var(--primary-color);
  font-weight: bold;
}

.dashboard__icon {
  height: 100px;
  width: 100px;
  border-radius: 15px;
  background: var(--neutral);
  font-size: 1.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: var(--trans);
}

.dashboard__icon:hover {
  background: linear-gradient(var(--primary-color), var(--secondary-color));
  box-shadow: var(--shadow);
}

.right__dashboard {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}

.right__dashboard img {
  max-width: 100%;
  height: auto;
  border-radius: 15px;
}